/* src/components/Controls.css */
.controls-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.control-group {
  margin-bottom: 15px;
}

.control-group h3 {
  margin: 5px 0;
}

.control-group button {
  margin-right: 5px;
  padding: 5px 10px;
  font-size: 14px;
  cursor: pointer;
}
