/* src/components/PopulationMap.css */
.population-map {
  margin-top: 20px;
}

.map-svg {
  border: 1px solid #ccc;
  background-color: #f0f8ff;
}

.icon {
  font-size: 16px;
  pointer-events: none;
}

.tree {
  color: darkgreen;
}

.bush {
  color: brown;
}

.rabbit {
  color: gray;
}

.wolf {
  color: red;
}
